<template>
  <div>
    <div class="searchs">
      <el-form :inline="true" @submit.native.prevent="onSubmit" :model="searchs" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="账号">
              <el-input v-model="searchs.keyword_search_value" placeholder="会员手机号"></el-input>
            </el-form-item>
            <el-form-item label="金额">
              <el-input v-model="searchs.begin_money"></el-input>
            </el-form-item>
            <el-form-item label="至">
              <el-input v-model="searchs.end_money"></el-input>
            </el-form-item>
            <el-form-item label="交易类型">
              <el-select v-model="searchs.type" clearable placeholder="请选择">
                <el-option v-for="(value, name) in types" :key="name" :label="value" :value="name"> </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="流水类型">
              <el-select v-model="searchs.stype" clearable placeholder="请选择">
                <el-option v-for="(value, name) in stypes" :key="name" :label="value" :value="name"> </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="交易状态">
              <el-select v-model="searchs.status" clearable placeholder="请选择">
                <el-option v-for="(value, name) in statuss" :key="name" :label="value" :value="name"> </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="交易日期">
              <el-date-picker
                v-model="addtime"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="pickerOptions"
              />
            </el-form-item>
            <el-form-item>
              <el-button type="primary" native-type="submit">查询</el-button>
            </el-form-item>
          </el-col>
          <el-col>
            收入：<span style="color: red;">{{ data.sum_add_money }}元</span> 支出：<span style="color: red;"
              >{{ data.sum_sub_money }}元</span
            >
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="订单号" align="center" />
      <el-table-column prop="cid" label="关联id" align="center" />
      <el-table-column prop="title" label="标题" align="center" />
      <el-table-column prop="remark" label="备注" align="center" />
      <el-table-column prop="name" label="姓名" align="center" />
      <el-table-column prop="mobile" label="手机号" align="center" width="150" />
      <el-table-column prop="type_name" label="交易类型" align="center" />
      <el-table-column prop="stype_name" label="流水类型" align="center" />
      <el-table-column prop="money" label="交易值" align="center" />
      <el-table-column prop="befor_money" label="交易前" align="center" />
      <el-table-column prop="after_money" label="交易后" align="center" />
      <el-table-column prop="addtime_date" label="交易时间" align="center" width="170" />
      <el-table-column prop="status_name" label="状态" align="center" />
      <!-- <el-table-column label="操作" min-width="300">
        <template slot-scope="scope">
          <el-button v-if="scope.row.status === 'D'" @click="setSuccess(scope.row)" type="success" size="mini"
            >成功</el-button
          >
          <el-button v-if="scope.row.status === 'D'" @click="setNot(scope.row)" type="warning" size="mini"
            >失败</el-button
          >
          <el-button v-if="scope.row.status === 'D'" @click="setMiss(scope.row)" type="primary" size="mini"
            >忽略</el-button
          >
          <el-button @click="removeItem(scope.row)" type="danger" size="mini">删除</el-button>
        </template>
      </el-table-column> -->
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
    <el-dialog title="会员详情" :visible.sync="dialogFormVisible">
      <el-form :model="detail">
        <el-form-item label="活动名称" label-width="80">
          <el-input v-model="detail" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import dayjs from 'dayjs'
import Fetch from '../../utils/fetch'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      dialogFormVisible: false,
      searchs: {
        keyword_search_value: '',
        begin_addtime: '',
        end_addtime: '',
        begin_money: '',
        end_money: '',
        type: '',
        stype: '',
      },
      statuss: null,
      types: null,
      stypes: null,
      loading: false,
      detail: {},
      all_money: '',
      tableData: [],
      count: 0,
      page_curren: 1,
      addtime: [],
      data: {},
      pickerOptions: {
        shortcuts: [
          {
            text: '本月',
            onClick(picker) {
              picker.$emit('pick', [new Date(), new Date()])
            },
          },
          {
            text: '今年至今',
            onClick(picker) {
              const end = new Date()
              const start = new Date(new Date().getFullYear(), 0)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '最近六个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setMonth(start.getMonth() - 6)
              picker.$emit('pick', [start, end])
            },
          },
        ],
      },
    }
  },
  watch: {
    addtime(val) {
      if (val) {
        this.searchs.begin_addtime = dayjs(val[0]).format('YYYY-MM-DD')
        this.searchs.end_addtime = dayjs(val[1]).format('YYYY-MM-DD')
      } else {
        this.searchs.begin_addtime = ''
        this.searchs.end_addtime = ''
      }
    },
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    FetchList() {
      this.loading = true
      Fetch('/fin/funds/search', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.data = r.data
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.types = r.data.config.type
          this.stypes = r.data.config.stype
          this.statuss = r.data.config.status
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    setSuccess(row) {
      this.$confirm('是否确认将状态修改为成功?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            Fetch('/fin/funds/update', { id: row.id, status: 'Y' })
              .then(() => {
                instance.confirmButtonLoading = false
                Message.success('操作成功')
                this.FetchList()
                done()
              })
              .catch(() => {
                instance.confirmButtonLoading = false
                Message.error('操作失败')
              })
          } else {
            done()
          }
        },
      })
    },
    setNot(row) {
      this.$confirm('是否确认将状态修改为失败?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            Fetch('/fin/funds/update', { id: row.id, status: 'N' })
              .then(() => {
                instance.confirmButtonLoading = false
                Message.success('操作成功')
                this.FetchList()
                done()
              })
              .catch(() => {
                instance.confirmButtonLoading = false
                Message.error('操作失败')
              })
          } else {
            done()
          }
        },
      })
    },
    setMiss(row) {
      this.$confirm('是否确认将状态修改为忽略?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            Fetch('/fin/funds/update', { id: row.id, status: 'D' })
              .then(() => {
                instance.confirmButtonLoading = false
                Message.success('操作成功')
                this.FetchList()
                done()
              })
              .catch(() => {
                instance.confirmButtonLoading = false
                Message.error('操作失败')
              })
          } else {
            done()
          }
        },
      })
    },
    removeItem(row) {
      this.$confirm('是否确认删除此条数据?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            Fetch('/fin/funds/remove', { id: row.id })
              .then(() => {
                instance.confirmButtonLoading = false
                Message.success('操作成功')
                this.FetchList()
                done()
              })
              .catch(() => {
                instance.confirmButtonLoading = false
                Message.error('操作失败')
              })
          } else {
            done()
          }
        },
      })
    },
  },
}
</script>
